.message {
    display: flex;
    align-content: flex-end;
    margin: 10px;
}
.avatar{
    height: 50px;
    width: 50px;
    border-radius: 50%;
}

.content {
    padding: 10px;

    border-radius: 5px 5px 5px 0;
    background-color: aquamarine;
}

.angle {
    width: 15px;
    height: 15px;
    background: radial-gradient(circle at 0 0, rgba(0, 0, 0,0) 15px, aquamarine 0);


}

.text {
    min-width: 150px;
    max-width: 300px;
    word-wrap: break-word;
}

.name {
    font-size: 20px;
    font-weight: bold;
    color: darkcyan;
}

.time {
    display: flex;
    justify-content: flex-end;
    color: darkcyan;
}